.header {
    background-color: #fff;
    font-size: 2.6rem;
    width: 100%;
    line-height: 44px;
    z-index: 10;
    position: fixed;
  .header-container {
    width: 100%;
    z-index: 10;
    transition: box-shadow 0.2s;
    display: flex;
    justify-content: center;
    .header-panel {
      height: 60px;
      width: 100%;
      max-width: 1200px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: solid #1c3a72 1px;
      .logo {
        padding: 0 10px;
        min-width: 175px;
        position: relative;
        &:hover {
          cursor: pointer;
        }
        img {
          width: 175px;
          height: 35px;
          position: absolute;
          top: 6px;
        }
      }
      .description {
        font-size: 20px;
        color: #384050;
        padding: 0 20px;
        &:hover {
          cursor: pointer;
        }
      }
      .language {
        .language-container {
          display: flex;
          padding: 0 10px;
          &:hover {
            background-color: #f7f8f9;
            border: 1px solid #c4ced7;
            border-radius: 2px;
            cursor: pointer;
          }
          .flag {
            display: flex;
            align-items: center;
            img {
              width: 20px;
              height: 15px;
            }
          }
          .description {
            font-size: 12px;
            color: #384050;
            padding: 0 8px 0 8px;
          }
        }
      }
    }
  }
}