footer {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #384050;
  position: absolute;
  bottom: 0px;
}
.footer-container {
  width: 100%;
  background-color: #384050;
  justify-content: center;
  max-width: 1200px;

  .footer-top {
    width: 100%;
    color: #ffff;
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    .contact-container {
      display: flex;
      font-size: 16px;
      align-items: center;
      .phone-container {
        display: flex;
        align-items: center;
        width: 200px;
        .phone-icon {
          margin-right: 10px;
          img {
            width: 30px;
          }
        }
      }
    }
    .social-container {
      display: flex;
      .social-item {
        margin-right: 10px;
        img {
          width: 32px;
        }
      }
    }
  }
  .bottom-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
    .footer-bottom {
      border-top: solid .5px #888;
      font-size: 12px;
      width: 600px;
      display: flex;
      justify-content: center;
      color: #ffffff;
      padding: 5px 0 15px 0;
      .rb {
        margin: 0 5px;
      }
      a {
        color: #ffffff;
        text-decoration: none;
        font-size: 12px;
      }
    }
  }
}