.row {
  display: flex;
  flex-direction: row;
}

.space-between {
  justify-content: space-between;
}

a {
  color: #0061af;
  font-size: 14px;
  text-decoration: none;
}

button {
  background-color: #0061af ;
  font-size: 16px;
  color: #ffffff;
  height: 40px;
  line-height: 30px;
  outline: none;
  border: none;
  &:hover {
    background-color: #F99E49;
    cursor: pointer;
  }
}

button.white-btn {
  background-color: #ffffff ;
  font-size: 14px;
  color: #576b88 !important;
  height: 45px;
  line-height: 30px;
  outline: none;
  border: 1px solid rgba(0,0,0,0);
  border-color: #7d8da4 !important;
  box-shadow: 1px 1px 3px 0 rgba(45,55,62,0.55);
  padding: 0 35px;
  letter-spacing: 0.05rem;
  border-radius: 2px;
  &:hover {
    background-color: #F99E49;
    cursor: pointer;
  }
}

.small-blue-btn {
  background-color: #0061af ;
  color: #ffffff;
  outline: none;
  font-size: 14px;
  padding: 0 35px;
  box-shadow: 1px 1px 3px 0 rgba(45,55,62,0.55);
  height: 45px;
  letter-spacing: 0.05rem;
  box-sizing: border-box;
  border-radius: 2px;
  border: 1px solid #5f94ff;
  &:hover {
    background-color: #F99E49;
    cursor: pointer;
  }
}
.toggle-input {
  width: 16px;
  position: absolute;
  right: -30px;
  &:hover {
    cursor: pointer;
  }
}

.bold {
  font-weight: 700;
}

.label {
  color: #0061af;
  font-weight: 700;
  font-size: 16px;
  margin-top: 10px;
}