.step-container {
  width: 100%;
  max-width: 1200px;
  position: relative;
  .form-auth {
    position: absolute;
    top: 150px;
    left: 100px;
  }
  .item-field {
    display: flex;
    border-bottom: solid #1c3a72 1px;
    color: #384050 !important;
    height: 36px;
    padding: 3px 58px 3px 30px;
    align-items: center;
    width: 312px;
    position: relative;
    //margin-top: 10px;
    .forget-password {
      color: #0061af;
      position: absolute;
      right: 5px;
      a {
        color: #0061af;
        font-size: 14px;
      }
    }
    .icon {
      position: absolute;
      left: 5px;
      top: 9px;
      img {

      }
    }
    input {
      border: none;
      &:focus {
        outline: none !important;
        border: none;
        &::placeholder {
          color: transparent;
        }
      }
      
    }
  }
  
  .links-container {
    width: 400px;
    margin-top: 10px;
  }
  .btn-container {
    width: 400px;
    button {
      width: 100%;
      margin-top: 25px;
    }
  }
}