.secure-container {
  position: absolute;
  top: 150px;
  font-size: 14px;
  color: #0061af;
  width: 100%;
  display: flex;
  justify-content: center;
  .secure-wrapper {
    width: 100%;
    max-width: 1200px;
    text-align: center;
    .secure-fields-container {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      .secure-field-description {
        margin-right: 64px;
      }
      input {
        border: none;
        border-bottom: solid #1c3a72 1px;
        outline: none;
        width: 400px;
        height: 30px;
        &:focus {
          outline: none !important;
          border: none;
          &::placeholder {
            color: transparent;
          }
        }
        
      }
      .secure-buttons-container {
        display: flex;
      }
    }
  }
}